'use client';

import React, { useState, useEffect, useCallback } from 'react';
import { useAuth } from '@/context/AuthContext';
import { listingsApi } from '@/lib/listings';
import { Listing } from '@shared/types';
import { MapPin, Clock, Users, Building2, Phone } from 'lucide-react';

const NGOClaimedPage: React.FC = () => {
  const { } = useAuth();
  const [listings, setListings] = useState<Listing[]>([]);
  const [loading, setLoading] = useState(true);

  const fetchClaimedListings = useCallback(async () => {
    try {
      setLoading(true);
      const response = await listingsApi.getNGOClaimedListings();
      setListings(response.listings || []);
    } catch (error) {
      console.error('Error fetching claimed listings:', error);
    } finally {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    fetchClaimedListings();
  }, [fetchClaimedListings]);

  if (loading) {
    return (
      <div className="max-w-7xl mx-auto py-6 px-4 sm:px-6 lg:px-8">
        <div className="text-center">Loading your claimed items...</div>
      </div>
    );
  }

  return (
    <div className="max-w-7xl mx-auto py-6 px-4 sm:px-6 lg:px-8">
      {/* Header */}
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-gray-900">
          Claimed Food Items
        </h1>
        <p className="mt-2 text-gray-600">
          Food items you have successfully claimed from hotels
        </p>
      </div>

      {/* Listings */}
      {listings.length === 0 ? (
        <div className="text-center py-12">
          <Users className="mx-auto h-12 w-12 text-gray-400" />
          <h3 className="mt-2 text-sm font-medium text-gray-900">No claimed items</h3>
          <p className="mt-1 text-sm text-gray-500">
            You haven&apos;t claimed any food items yet. Browse available listings to get started.
          </p>
        </div>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {listings.map((listing) => (
            <div key={listing._id} className="bg-white rounded-lg shadow-md overflow-hidden">
              <div className="p-6">
                <div className="flex items-start justify-between mb-3">
                  <h3 className="text-lg font-semibold text-gray-900">
                    {listing.title}
                  </h3>
                  <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-blue-100 text-blue-800">
                    Claimed
                  </span>
                </div>
                
                <p className="text-gray-600 text-sm mb-4">
                  {listing.description}
                </p>
                
                {/* Food Details */}
                <div className="space-y-3 mb-4">
                  <div className="flex items-center text-sm">
                    <Users className="h-4 w-4 text-gray-500 mr-2" />
                    <span className="font-medium text-gray-900">
                      {listing.quantity.amount} {listing.quantity.unit}
                    </span>
                    <span className="ml-2 px-2 py-0.5 bg-gray-100 text-gray-700 rounded text-xs capitalize">
                      {listing.foodType}
                    </span>
                  </div>
                  
                  <div className="flex items-center text-sm">
                    <Clock className="h-4 w-4 text-gray-500 mr-2" />
                    <span className="text-gray-700">
                      Claimed on {listing.claimedAt ? new Date(listing.claimedAt).toLocaleDateString() : 'Unknown'}
                    </span>
                  </div>
                  
                  <div className="flex items-start text-sm">
                    <Building2 className="h-4 w-4 text-gray-500 mr-2 mt-0.5" />
                    <div>
                      <p className="text-gray-700 font-medium">{listing.hotel.organizationName}</p>
                      <p className="text-gray-500 flex items-center">
                        <Phone className="h-3 w-3 mr-1" />
                        {listing.hotel.phone}
                      </p>
                    </div>
                  </div>
                  
                  <div className="flex items-start text-sm">
                    <MapPin className="h-4 w-4 text-gray-500 mr-2 mt-0.5" />
                    <p className="text-gray-500">{listing.pickupLocation.address}</p>
                  </div>
                </div>
                
                {/* Allergens */}
                {listing.allergens && listing.allergens.length > 0 && (
                  <div className="mb-4">
                    <p className="text-sm font-medium text-gray-700 mb-1">Contains allergens:</p>
                    <div className="flex flex-wrap gap-1">
                      {listing.allergens.map((allergen: string) => (
                        <span
                          key={allergen}
                          className="inline-flex items-center px-2 py-0.5 rounded text-xs font-medium bg-yellow-100 text-yellow-800"
                        >
                          {allergen}
                        </span>
                      ))}
                    </div>
                  </div>
                )}
                
                {/* Special Instructions */}
                {listing.specialInstructions && (
                  <div className="mb-4">
                    <p className="text-sm font-medium text-gray-700">Special Instructions:</p>
                    <p className="text-sm text-gray-600 mt-1">{listing.specialInstructions}</p>
                  </div>
                )}
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default NGOClaimedPage;
